import Image from 'next/image';
import styles from './page.module.scss';

export default async function Object({ params }: { params: Promise<{ id: string }>}) {
  const { id } = await params;

  const response = await fetch(`http://10.8.0.2:3001/objects/${id}`)
  const parsed = await response.json();

  return (
    <div className={styles.wrapper}>
      {parsed.files.map((file: any) => 
        <div className={styles.imageWrapper} key={file.id}>
          <Image
            alt='Object picture'
            src={`http://10.8.0.2:3001/${file.path}`}
            fill
          />
        </div>
      )}
    </div>
  );
}
